.block{padding:45px;background:#ffffff;box-shadow:0px 0px 25px rgba(0,0,0,0.04204);border-radius:3px}

.button--large{height:56px !important}

.avatar{align-items:center;justify-content:center;border-radius:50px;width:50px;height:50px;margin-right:15px;display:flex}.avatar__letter{font-size:20px;font-weight:bold;margin-top:-1px}

.navbar{background-color:#001529;overflow:hidden;position:fixed;bottom:0;width:100%;display:flex;justify-content:space-between;align-items:center}.navbar a{float:left;display:block;color:rgba(255,255,255,0.65);text-align:center;padding:14px 16px;text-decoration:none;font-size:14px}.navbar a:hover{color:white}.navbar a.active{background-color:#1890ff;color:white}

.auth{display:flex;justify-content:center;align-items:center;height:100%}.auth__top{text-align:center;margin-bottom:50px}.auth__top h2{font-size:28px;margin-bottom:5px}.auth__top p{font-size:18px;line-height:21px;letter-spacing:0.1px;opacity:0.5}.auth__register-link{text-align:center;display:block;font-size:16px;letter-spacing:0.1px;color:#adadad}.auth__success-block{display:flex;align-items:center;justify-content:center;flex-direction:column;text-align:center;height:400px}.auth__success-block svg{width:48px;height:48px}.auth__success-block h2{margin-top:10px}.auth__success-block p{color:#adadad}.auth .button{text-transform:uppercase;font-weight:500;font-size:14px;letter-spacing:0.1px;width:100%}.verify-block__loading{text-align:center}

.orders{display:flex;justify-content:space-between;align-items:center}

.clients{display:flex;justify-content:flex-end}

.foods{display:flex;justify-content:flex-end}

.order{display:flex;justify-content:space-between;align-items:center}.order .ant-badge>sup{font-size:10px}

.order-form__form{display:flex;flex-direction:column}.order-form__form>div{width:100%}

